header {
	background: $main-green; }
.logo {
	font-size: 20px;
	padding: 50px 0px;
	margin: 0; }
.logo__link {
	display: inline-flex;
	align-items: center;
	text-decoration: none; }
.logo__link span {
	padding: 0 10px;
	font-style: normal;
	font-weight: 600;
	font-size: 20px;
	padding-bottom: 12px;
	line-height: 27px;
	text-align: center;
	color: $main-white; }
.nav__container {
	margin-inline-end: 2px; }
.nav-list__item {
	list-style: none; }
.nav-list__link {
	text-decoration: none;
	font-size: 16px; }
nav {
	display: flex;
	position: relative;
	justify-content: space-around;
	align-items: center; }
.nav__container {
	height: 100%; }

.nav__container {
	.nav-list {
		display: flex;
		margin: 0px;
		padding: 0px;
		&__link {
			font-weight: 400;
			font-size: 20px;
			line-height: 27px;
			text-align: center;
			display: block;
			padding: 20px;
			padding-top: 1px;
			color: $main-white; }
		&__link:hover {
			color: $main-yellow;
			scale: 1.2;
			transition: all .2s ease; } } }
