.section-home {
	margin: 0 auto;
	min-height: 660px;
	padding: 55px 0px;
	background: $main-green;
	&__title {
		margin: 0 auto;
		color: $main-white;
		font-style: normal;
		font-weight: 700;
		font-size: 64px;
		line-height: 87px;
		text-align: center;
		padding-right: 7px; }
	&__subtitle {
		margin-top: 35px;
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		line-height: 27px;
		text-align: center;
		color: $main-white; }
	.button__wrap {
		margin-top: 45px;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		.button {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 211px;
			height: 73px;
			background: $main-white;
			cursor: pointer;
			border: none;
			margin: 0 auto; }
		span {
				display: inline-block;
				font-weight: 700;
				font-size: 20px;
				line-height: 27px;
				text-align: center;
				color: $maim-black; }
		img {
			margin-left: -20px; }

		.button:hover {
			background-color: $main-yellow;
			scale: 1.1;
			transition: all .2s ease; } } }
