@media (max-width: 1200px) {
	.logo {
		margin: 0 10px;
		&__link {
			text-decoration: none; }
		span {
			font-size: 17px; } }
	.section-home__title {
		margin: 0 15px; } }

@media (max-width: 990px) {
	.logo__link {
		span {
			font-size: 15px; } }
	.nav__container {
		.nav-list__link {
			font-size: 15px; } }
	.section-home__title {
		font-size: 50px; }
	.section-home__subtitle {
		margin-top: 100px; }
	.section-home .button__wrap .button {
		margin-top: 32px; } }

@media (max-width: 767px) {
	.nav {
		align-items: flex-start;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		overflow: hidden;
		position: fixed;
		width: 100%;
		background-color: rgba(0,0,0, .8); }
	.logo {
		padding: 25px; }
	.nav__container.active {
		display: flex;
		transition: 0.5s all;
		.nav-list__link {
			font-size: 18px; } }
	.nav__container {
		width: 100%;
		display: none;
		background-color: rgba(255, 255, 255, 0.1);
		backdrop-filter: blur(20px);
		.nav-list {
			width: 100%;
			flex-direction: column;
			&__item {
				text-align: center;
				padding-top: 12px;
				&__link {
					padding: 8px 16px; } } } }
	/* menu icon */
	.menu-button {
		position: absolute;
		top: 30px;
		right: 30px;
		display: none;
		flex-direction: column;
		justify-content: space-between;
		width: 30px;
		height: 30px; }
	.menu-button.active .menu-button__bar:nth-child(2) {
		opacity: 0; }
	.menu-button.active .menu-button__bar:nth-child(1) {
		transform: translateY(13px) rotate(-45deg); }
	.menu-button.active .menu-button__bar:nth-child(3) {
		transform: translateY(-13px) rotate(45deg); }
	.menu-button__bar {
		display: block;
		height: 2px;
		width: 100%;
		margin-bottom: 2px;
		transition: 0.5s all;
		background-color: $main-white; }
	.menu-button {
		display: flex; }
	.section-home__title {
		margin-top: 92px;
		font-size: 42px; }
	.section-home__subtitle {
		padding: 0 15px; }
	.section-home .button__wrap {
		margin-top: 75px;
		.button {
			width: 185px;
			margin: 0 auto; } } }

@media (max-width: 575px) {
	.section-home__title {
		font-size: 34px;
		margin-top: 0px; }
	.section-home__subtitle {
		margin-top: 65px; }
	.section-home__title {
		margin-top: 38px; } }
